<template>
<div class="hot">
  <div class="hottop">
    <div class="hottoppic">
      <div class="hottoppict"></div>
      <div class="hotData">更新日期:{{ this.newsongs.updateTime }}</div>
    </div>
  </div>
  <ul>
    <HomeTitle>热门音乐</HomeTitle>
      <ul class="home-songlist">
        <SongListItem
        v-for="(item,index) in newsongs.tracks" 
      :key="item.id"
      :item="item"
      @change-current-song="$emit('change-current-song',$event);$emit('change-current-play-list',newsongs.tracks)"
      :currentSongId="currentSongId"
      :playing="playing"
      :class="{ lt3:index < 3 }"
        >{{ index+1 }}</SongListItem>
      </ul>
  </ul>
</div>
</template>

<script>
import  HomeTitle from "@/components/HomeTitle.vue";
import SongListItem from "@/components/SongListItem.vue";

export default {
  components: {
        HomeTitle,
    SongListItem,
  },
  props:{
    currentSongId:{
      type:Number,
    },
    playing:Boolean,
  },
  data: function () {
    return {
      detail: null,
       personalizeds: [],
      newsongs:[],
      dtime:"",
    };
  },

 created: function () {
      this.axios
      .get("/playlist/detail?id=3778678")
      .then((res)=>{
        // console.log(res.data.playlist)
        this.newsongs=res.data.playlist;
        var date = new Date(this.newsongs.updateTime );
        var M = (date.getMonth()+1 < 10 ? ' '+(date.getMonth()+1) : date.getMonth()+1) + '月';
        var D =( date.getDate() < 10 ? '0'+(date.getDate()) : date.getMonth()) + '日 ';
        console.log(M+D);
        this.newsongs.updateTime=M+D;
      });
    },
};
</script>

